            ></el-col>
            <el-col :span="6">样品数量</el-col>
            <el-col :span="6"
              ><el-input v-model="formData.sampleCount"></el-input
            ></el-col>
          </el-row>
          <el-row>
            <el-col :span="6">企划克重</el-col>
            <el-col :span="6"
              ><el-input v-model="formData.planWeight"></el-input
