          <el-row class="focus-point">
            <el-col :span="6">注意点</el-col>
            <el-col :span="18"
              ><el-input
                type="textarea"
                autosize
                v-model="formData.notice"
              ></el-input
            ></el-col>
          </el-row>
        </el-col>
        <!-- 中间表格部分 -->
        <el-col :span="15">
          <div class="flower-row">
            <el-row>
              <el-col class="left-column" :span="3">花型配色</el-col>
              <el-col :span="16">配色描述</el-col>
              <el-col :span="5">备注</el-col>
            </el-row>
            <el-row>
              <el-col :span="3">花型</el-col>
              <el-col :span="4">主色</el-col>
              <el-col :span="12">配色</el-col>
              <el-col :span="5"></el-col>
            </el-row>
            <el-row v-for="item in flowerMax" :key="item">
              <el-col :span="3"><el-input v-model="formData.patterns[item-1].colorKey"></el-input></el-col>
              <el-col :span="4"><el-input v-model="formData.patterns[item-1].mainColor"></el-input></el-col>
              <el-col :span="2" v-for="subitem in 6" :key="subitem">
                <el-input v-model="formData.patterns[item-1].samplePlanColorDics[subitem-1].colorValue"></el-input>
              </el-col>
              <el-col :span="5"><el-input v-model="formData.patterns[item-1].comment"></el-input></el-col>
            </el-row>
          </div>
          <el-row>
            <el-col :span="19"
              >规格：
              <el-radio-group v-model="checked">
                <el-radio :label="3">一般量法</el-radio>
                <el-radio :label="6">中心点量法</el-radio>
                <el-radio :label="9">交叉量法</el-radio>
              </el-radio-group>
            </el-col>
            <el-col :span="5">量法显示</el-col>
          </el-row>
          <!-- 规格尺寸 -->
          <div class="size-row">
            <!-- 标题列 -->
            <el-col style="border: 0px" :span="3">
              <div>
                <el-col v-for="item in itemstop" :key="item.index">
                  <el-row>{{ item }}</el-row>
                </el-col>
              </div>
            </el-col>
            <el-col style="border: 0px"  :span="2" v-for="item in 7" :key="item">
              <div>
                <el-col v-for="item1 in propList" :key="item1">
                  <el-input v-model="formData.measures[item-1][item1]"></el-input>
                </el-col>
              </div>
            </el-col>
            <el-col style="border: 0px" :span="2">
              <div>
                <el-col v-for="item in itemsAllowance" :key="item.index">
                  <el-row>{{ item }}</el-row>
                </el-col>
              </div>
            </el-col>
            <el-col :span="5">111</el-col>
          </div>
        </el-col>
      </el-row>
      <!-- 换页区 -->
      <el-row v-show="!isTableDisplay" id="middle">
        <el-col>
          <div class="flower-row">
            <el-row>
              <el-col class="left-column" :span="3">花型配色</el-col>
              <el-col :span="16">配色描述</el-col>
              <el-col :span="5">备注</el-col>
            </el-row>
            <el-row>
              <el-col :span="3">花型</el-col>
              <el-col :span="4">主色</el-col>
              <el-col :span="12">配色</el-col>
              <el-col :span="5"></el-col>
            </el-row>
            <el-row v-for="item in flowerMax" :key="item">
              <el-col :span="3"><el-input v-model="formData.patterns[item+9].colorKey"></el-input></el-col>
              <el-col :span="4"><el-input v-model="formData.patterns[item+9].mainColor"></el-input></el-col>
              <el-col :span="2" v-for="subitem in 6" :key="subitem">
                <el-input v-model="formData.patterns[item+9].samplePlanColorDics[subitem-1].colorValue"></el-input>
              </el-col>
              <el-col :span="5"><el-input v-model="formData.patterns[item+9].comment"></el-input></el-col>
            </el-row>
          </div>
          <el-row>
            <el-col :span="19"
              >规格：
              <el-radio-group v-model="checked">
                <el-radio :label="3">一般量法</el-radio>
                <el-radio :label="6">中心点量法</el-radio>
                <el-radio :label="9">交叉量法</el-radio>
              </el-radio-group>
            </el-col>
            <el-col :span="5">量法显示</el-col>
          </el-row>
          <!-- 上半部分 -->
          <el-row class="size-row">
            <el-col style="border: 0px" :span="3">
              <div>
                <el-col v-for="item in itemstop" :key="item.index">
                  <el-row>{{ item }}</el-row>
                </el-col>
              </div>
            </el-col>
            <el-col style="border: 0px"  :span="2" v-for="item in 7" :key="item">
              <div>
                <el-col v-for="item1 in propList" :key="item1">
                  <el-input v-model="formData.measures[item+6][item1]"></el-input>
                </el-col>
              </div>
            </el-col>
            <el-col style="border: 0px" :span="2">
              <div>
                <el-col v-for="item in itemsAllowance" :key="item.index">
                  <el-row>{{ item }}</el-row>
                </el-col>
              </div>
            </el-col>
            <el-col :span="5">111</el-col>
          </el-row>
        </el-col>
      </el-row>
      <!-- 按钮区 -->
      <el-row type="flex" justify="end">
        <el-checkbox style="line-height: 40px" v-model="isTableDisplay"
          >不对外发布</el-checkbox
        >
        <el-button>提交</el-button>
        <el-button>取消</el-button>
      </el-row>
    </div>
  </div>
</template>

<script>
import { GetSingleSamplePlan } from "network/proofSingle.js";

import { MeasureItem, FormData,MainFlower,ComplementaryFlower} from "service/Business/proposal.js";

export default {
  data() {
    return {
      options: [
        {
          value: "ZS00232",
        },
        {
          value: "EW00232",
        },
        {
          value: "BU00232",
        },
      ],
      measures: [{}, {}, {}, {}, {}],
      object1: {},
      itemstop: [
        "平铺尺寸、尺码",
        "A 脚底长",
        "B 上统长",
        "C 罗口长",
        "D 罗口宽",
        "E 上统宽",
        "F 刺绣中心距罗口",
        "G 脚底宽",
        "拉伸尺寸、尺码",
        "A 脚底长",
        "B 上统长",
        "C 罗口长",
        "D 罗口宽",
        "E 上统宽",
        "F 刺绣中心距罗口",
        "G 脚底宽",
      ],
      itemsbottom: [],
      propList: [
        "size",
        "sizeStretch",
        "footLengthNormal",
        "footLengthStretch",
        "upperLengthNormal",
        "upperLengthStretch",
        "luoKouWidthNormal",
        "luoKouWidthStretch",
        "luoKouLengthStretch",
        "luoKouLengthNormal",
        "upperWidthNormal",
        "upperWidthStretch",
        "footWidthNormal",
        "footWidthStretch",
        "distanceNormal",
        "distanceStretch",
      ],
      itemsAllowance: ["公差", "±0.5", "±0.5", "", "±0.5", "±0.5", "", "","公差", "±0.5", "±0.5", "", "±0.5", "±0.5", "", ""],
      value: "",
      checked: 3,
      isTableDisplay: true,
      proposalHeaderData: [],
      proposalMiddleData: {
        data: "2018.12.3",
      },
      flowerdata: [
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
        { type: "", main: "", secondary: "" },
      ],
      formData:{},
      flowerMax:10,
      flowerKeyLength:6,
      sizeMax:8,
      measuresMax:18,
      patternMax:20,
      isOverLoad:false,
      tableData:null
    };
  },
  computed: {
    pictureLink() {
      switch (this.checked) {
        case 3:
          return "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg";
          break;
        case 6:
          return require("@/assets/logo.png");
      }
    },
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      if (this.$route.query.iid === undefined) {
        let formData = new FormData();
        this.formData = formData;
        this.tableDataInital();
      } else {
        var data = this.$route.query.iid;
        GetSingleSamplePlan(data).then((res) => {
          this.formData = res.result.items[0];
          this.tableDataInital();
        });
      }
    },
    //找到最大值
    /* getMax() {
      this.measuresNum = this.formData.measures.length;
      this.patternMax = this.formData.patterns.length;
    }, */
    //添加尺寸数据预处理
    measureDataPreProcess(){
      let i = this.formData.measures.length;
      for(i;i < this.measuresMax;i++){
        let item = new MeasureItem();
        this.formData.measures.push(item); 
      }
    },
    //花型尺寸数据预处理
    flowerDataPreProcess(data){
      console.log('1213')
      let arr = new Array();
      const keyLen = this.flowerKeyLength;
      const max = this.patternMax;
      data = data.map(item=>{
        if(item.samplePlanColorDics.length<keyLen){
          for(let i=item.samplePlanColorDics.length;i<keyLen;i++){
            let keyflower = new ComplementaryFlower();
            item.samplePlanColorDics.push(keyflower)
          }
          return item
        }else{
          if(item.samplePlanColorDics.length>keyLen){
            let itemcopy = JSON.parse(JSON.stringify(item));
            let concatitem = item.samplePlanColorDics.slice(keyLen-1,item.length-1);
            item.samplePlanColorDics = item.samplePlanColorDics.slice(0,keyLen-1);
            itemcopy.samplePlanColorDics = concatitem;
            arr.push(itemcopy);
            return item
          }
        }
      });
      data = data.concat(arr);
      for(let j=data.length;j < max;j++){
        let item = new MainFlower();
        for(let s=1;s<7;s++){
          let subitem = new ComplementaryFlower();
          item.samplePlanColorDics.push(subitem)
        }
        data.push(item); 
      }
      this.formData.patterns = data;
      console.log('1213')
    },
    //数据初始化
    tableDataInital(){
      this.measureDataPreProcess();
      console.log('1213')
      this.flowerDataPreProcess(this.formData.patterns);
    },
    uploadfile(file, fileList) {
      this.proposalHeaderData = fileList;
      for (var i = 0; i < this.proposalHeaderData.length; i++) {
        this.proposalHeaderData[i].index = i;
      }
    },
    deletefile() {
      for (var i = 0; i < this.formData.length; i++) {
        this.proposalHeaderData.splice(this.formData[i].index, 1);
      }
      for (var i = 0; i < this.proposalHeaderData.length; i++) {
        this.proposalHeaderData[i].index = i;
      }
    },
    handlePictureCardPreview(){},
    handleRemove(){}
  },
};
</script>

